import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import Nav from 'react-bootstrap/Nav';
import { tab } from "@testing-library/user-event/dist/tab";
import { addItem } from "../store";
import { useDispatch, useSelector } from 'react-redux';

function Card(props) {

    let [tab, setTab] = useState(0);
    let [input, setInput] = useState('');
    let dispatch = useDispatch()
    // let  = props.shoes.find(function (a) {
    //     return a.id == id
    // });


    // useEffect(()=>{
    //     let  = localStorage.setItem('watched')
    //      = JSON.parse()
    //     .push(.id)
    //      = new Set()  //
    //      = Array.from() // 
    //     localStorage.getItem('watched', JSON.stringify())
    // })

    useEffect(() => {
        if (isNaN(input) == false) {

        }
    }, [input])


    
    let { id } = useParams();
    


    return (<div className="container">
        <input onChange={((e) => { setInput(e.target.value) })} />
        <div className="row">
            <div className="col-md-6">
                <img src={'https://codingapple1.github.io/shop/shoes1.jpg'} width="100%" />
            </div>
            <div className="col-md-6">
                <h4 className="pt-5">{props.shoes[id].title}</h4>
                <p>{props.shoes[id].content}</p>
                <p>{props.shoes[id].price + ''}</p>
                <button className="btn btn-danger"onClick={(a, i)=>{
                    dispatch(addItem({
                        id : props.shoes[0].id,
                        name : props.shoes[0].title,
                        count: 1,
                    }))
                }}></button>
            </div>
        </div>
        <Nav variant="tabs" defaultActiveKey="link0">
            <Nav.Item>
                <Nav.Link onClick={()=>{
                    setTab(0)
                }}eventKey="link0">0</Nav.Link>
            </Nav.Item>
            <Nav.Item>
                <Nav.Link onClick={()=>{
                    setTab(1)
                }} eventKey="link1">1</Nav.Link>
            </Nav.Item>
            <Nav.Item>
                <Nav.Link onClick={()=>{
                    setTab(2)
                }} eventKey="link2">2</Nav.Link>
            </Nav.Item>
        </Nav>
        
        <Cookie tab={tab}/>
    </div>
    )
}


function Cookie({tab}){

    let [fade, setFade] = useState('');

    useEffect(()=>{
        setTimeout(()=>{ setFade('after')}, 100)
        return ()=>{
            setFade('')
        }
    }, [tab])
    return (<div className={`before ${fade}`}>
        { [<div>0</div>, <div>1</div>, <div>2</div>][tab] }
    </div>)
}





export { Card };

export { tab };
